import React, { Component } from "react";
import Posts from './Posts.js'
import './FullPost.css';
import { unmountComponentAtNode } from "react-dom";



class FullPost extends Component {

  constructor(props) {super(props);this.handleClick = this.handleClick.bind(this);
  	this.state = {            
            Posts:Posts            
        };}

  handleClick() {unmountComponentAtNode(document.getElementById('fullBlog'));document.getElementById('fullBlog').remove()}
 
  render() {
       const {Posts} = this.state;
  		function post(){

		return Posts.map((image) =>{

			return (
                 <div className="post" id={image.title.replace(/\s/g, "").replace('$','').replace('.','').replace('()','').toLowerCase()}>
                  <em>{image.data}</em>
                  <h2>{image.title}</h2>
                  <div className="featured" style={{backgroundImage:`url(${image.image})`}}></div>
                  <p>{ image.content }</p>
  
                 </div>
				)
		})

	}
	return(
       <div className="full-post">
        <button id="unm" onClick={this.handleClick}>x</button>
        {post()}

       </div>

	)
}
}
export default FullPost;
