import React from 'react'
import './Arrow.css';
import $ from 'jquery'
import addBreak from './blog/Article' 

export default function DownButton() {
	return(

       <a id="cblog" href="#blog"><i className="arrow down" onClick={function(e){overflowV();addBreak()}}></i></a>

		)

}

export function overflowV(){
		document.body.style.overflow = 'visible';
		$('html, body').animate({ scrollTop: 300 }, 300);
		$('#blog').fadeIn();

}



