import Stories from './Storie'
import $ from 'jquery'
import React, { Component } from "react";
import ReactDOM from "react-dom";
import { unmountComponentAtNode } from "react-dom";



class Story extends Component {

  constructor() {super();this.handleClick = this.handleClick.bind(this);}
handleClick() {unmountComponentAtNode(document.getElementById('storie'));}
  render() {
  	const escalaCinza = {filter:this.props.filter}
	const hand = {cursor:this.props.cursor || 'pointer'}

  		return(
	<div className="Profile">
			<div className="Profilefoto" onClick={function(e){loadStorie(); progressBar();getRid()}} style={hand}>
				<div className="border" style={escalaCinza}></div>
			</div>

			<div className="ProfileText">
				<h2>{this.props.nome}</h2>
				<p>{this.props.position}</p>
				<br/><br/>
				<a href={this.props.aInsta} target="_blank" rel="noopener noreferrer">{this.props.insta} | </a>
				<a href={this.props.aTwitter} target="_blank" rel="noopener noreferrer">{this.props.twitter} | </a>
				<a href={this.props.aDeviantart} target="_blank" rel="noopener noreferrer">{this.props.deviantart}</a>
			</div>
			
	</div>
	)
  }
}

export default Story;
const progress = '100%';
export function loadStorie(){ReactDOM.render(<Stories button="X" />,document.getElementById('storie'))}   

var fechar

export function getRid(){ fechar = setTimeout(function(){unmountComponentAtNode(document.getElementById('storie'))},10000) }

export function progressBar(){
		setTimeout(function(){ 
			$('.loader').fadeOut(1000); 
			$('.bar').css('width',progress)},1 )}

export {fechar}

