 { 
 image:require("./stonks.jpg").default, 
 title:'$.automatizar()', 
 data: 'novembro 2, 2020', 
 content:"Venhamos e convenhamos, JQuery está velho, porém ainda eficaz e gerando eficiência. Desde que comecei a trabalhar, mais especificamente com design sempre me deparei com gerenciadores de tarefas como asana, trello, etcetera etcetera. Depois de algum tempo, ou em tempos de correria (covid-19) consigo perceber melhor padrões repetitivos nas tarefas que executo. Muitos destes padrões são preenchimento de dados em planilhas, abas, cálculos simples e etc …Por que Jquery, e não linguagens mais atuais e que estão na moda?Simples, sua motto é ‘The write less, Do more’, é possível economizar tempo ao pensar numa solução com Jquery, e tornar trabalhos repetitivos mais eficientes. Escrevendo menos código fica mais fácil entender o pensamento por traz da resolução do “problema”, basta baixar a biblioteca de 85 kb e linkar ao html, ou em qualquer gerenciador de scripts em navegadores modernos.O preenchimento de datas (principalmente se não houver um date picker, ou barra automática) torna-se desconfortável num grande volume, e principalmente em laptops onde não há teclado numérico. Com Jquery é possível fazê-las de forma automática com $(function(){}).Mesmo em campos de pesquisa onde se digita carácteres especiais com shift ou alt, um simples botão com .val() determinado facilita, e não gastamos energia procurando as teclas desejadas.Textos e valores que são repetidos durante o processo de cadastros de produtos em e-commerces, são preenchidos ao carregar a página, ou estão dispostos de uma forma de mais fácil acesso, como um simples clique, sem esperar todo o tempo de loading de uma plataforma pesada com muitos recursos.Utilizar varias plataformas diferentes te coloca em uma situação de confusão, onde cada uma tem sua UI e UX, umas mais modernas outras a lá internet em 2009. Com Jquery foi possível identificar com cores utilizando .css(‘background’,’#ff0’) processos que teria que ser clicado e aberto uma outra aba/janela (esperar o tempo de carregamento plataformas pesadas).O simples trabalho de copiar e colar informações de uma aba para outra, ou de um software para outros torna evidente os padrões da atividade e suas características quando feitas em escalas acima de 10.Trabalhar com software escrito em linguagem braba (ou mal escrito mesmo), pode te dar muitos insights na sua maneira de trabalhar, principalmente quando duas tecnologias anacrônicas se encontram… “Preciso copiar uma chave de NF-E de 44 caracteres e inserir no e-commerce para que o processo da venda continue, porém com um específico software isso não funciona legal. Ele tem o campo com a chave, e ao dar o o click duplo para selecionar tudo o conteúdo do input, é selecionado espaços em brancos que precisam ser apagados, (isso é chato)”. A solução foi simples, limitei o input que vai receber o texto para a quantidade de caracteres da chave. $('input#...').attr('max-lenght','44')É sabido que uma linguagem de programação não te define, e que temos que aprender a aprender, mas Jquery além de facilitar, e tornar eficiente o que tem padrões, tem uma sintaxe sexy, e uma semântica fácil e soa muito legal…$(jQuery).on(‘life’,function() {facilitate()})jQuery is a JavaScript library designed to simplify HTML DOM tree traversal and manipulation, as well as event handling, CSS animation, and Ajax. It is free, open-source software using the permissive MIT License. As of May 2019, jQuery is used by 73% of the 10 million most popular websites. Wikipedia"}
